@import "css3";

.loading-container {
    position: fixed;
    top: 45%;
    left: 50%;
    @include translate(-50%, -50%);
    & > p {
        font-size: 0;
        margin-top: 5px;
        span {
            display: inline-block;
            font-size: 15px;
        }
        .progress-bar {
            margin-left: 5px;
        }
    }
}

.loading {
    height: 55px;
    text-align: center;
    font-size: 0;
    & > div {
        display: inline-block;
        width: 6px;
        height: 100%;
        background-color: #282828;
        margin-right: 5px;
        @include animation(loading 1.2s infinite ease-in-out);
    }
    .loading-item2 {
        @include animation-delay(-1.1s);
    }
    .loading-item3 {
        @include animation-delay(-1.0s);
    }
    .loading-item4 {
        @include animation-delay(-0.9s);
    }
    .loading-item5 {
        @include animation-delay(-0.8s);
    }
}

@include keyframes(loading) {
    0%, 40%, 100% {
        @include scaleY(0.5);
    }
    20% {
        @include scaleY(1);
    }
}