@import "reset";
@import "base";
@import "css3";
@import "header_bar";
@import "loading";

$placeHolderColor: #a0a0a0;

.container-fade {
    opacity: 0.6;
}

.upload-area {
    margin-top: 35px;
    & > h2 {
        font-size: 20px;
        margin-bottom: 15px;
        font-weight: bold;
        padding-left: 10px;
    }
}

.upload-container {
    background-color: #fff;
    padding: 35px 0;
    & > div {
        display: none;
    }
    .show {
        display: block;
    }
}

.upload-progress-bar {
    width: 80%;
    margin: 0 auto;
    font-size: 0;
    text-align: center;
    & > li {
        display: inline-block;
    }
}

.progress-point {
    position: relative;
    width: 63px;
    height: 63px;
    vertical-align: middle;
    img {
        position: relative;
        width: 100%;
        z-index: 2;
        display: none;
    }
    i {
        position: absolute;
        top: 45%;
        left: 50%;
        width: 20px;
        height: 20px;
        background-color: #282828;
        border-radius: 50%;
        @include translate(-50%, -50%);
    }
    p {
        margin-top: 10px;
        font-size: 14px;
        display: none;
    }
}

.progress-point-show {
    img {
        display: block;
    }
    p {
        display: block;
    }
}

.progress-join-line {
    width: 35%;
    height: 2px;
    margin: 0 10px;
    background-color: #282828;
}

.add-file-container {
    width: 60%;
    margin: 70px auto 0;
    border: 2px dashed #bebebe;
    background-color: #f2f2f2;
    padding: 40px 0;
    p {
        color: #8c8c8c;
        line-height: 1.5;
        font-size: 14px;
        text-align: center;
    }
}

.upload-file-component {
    text-align: center;
    margin-bottom: 28px;
    label {
        display: inline-block;
        padding: 15px 40px;
        background-color: #ee344a;
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
    }
    input[type="file"] {
        display: none;
    }
}

.upload-file-form {
    width: 60%;
    margin: 0 auto;
    padding-top: 50px;
}

.upload-file-item {
    font-size: 0;
    margin-bottom: 20px;
    text-align: center;
    h3,
    input,
    textarea {
        font-size: 15px;
    }
    h3 {
        display: inline-block;
        vertical-align: top;
        width: 12%;
        margin-right: 25px;
        text-align: right;
        line-height: 1.5;
    }
}

.file-input-item {
    display: inline-block;
    vertical-align: top;
    width: 70%;
    @include inputPlaceholder($placeHolderColor);
    input,
    textarea {
        display: block;
        width: 100%;
        height: 20px;
        border: 1px solid #ccc;
        //@include box-sizing(border-box);
        padding: 5px 10px;
    }
    textarea {
        resize: none;
        height: 90px;
    }
    p {
        padding: 10px 0 10px 10px;
        text-align: left;
        font-size: 14px;
        color: #ee344a;
        display: none;
    }
}

.select-box {
    width: 50%;
    font-size: 15px;
    .current-selected {
        position: relative;
        height: 20px;
        padding: 5px 10px;
        border: 1px solid #ccc;
        line-height: 20px;
        cursor: pointer;
        i {
            position: absolute;
            border: 5px solid #fff;
            border-top: 5px solid #282828;
            top: 50%;
            right: 5px;
            @include translateY(-20%);
        }
    }
    .selected-placeholder {
        color: $placeHolderColor;
    }
    .select-options {
        max-height: 200px;
        //overflow-y: scroll;
        border: 1px solid #ccc;
        border-top-width: 0;
        li {
            padding: 5px 0;
            cursor: pointer;
            &:hover {
                background: #e2e2e2;
            }
        }
    }
}

.show-error-info {
    margin-bottom: -10px;
    input,
    textarea {
        border: 1px solid #ee344a;
    }
    p {
        display: block;
    }
    .current-selected {
        border: 1px solid #ee344a;
    }
}

.upload-file-button {
    padding: 15px 35px;
    background-color: #ee344a;
    color: #fff;
    font-size: 20px;
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
    &.uploading {
        opacity: 0.8;
    }
}

.upload-success-container {
    padding-top: 30px;
    width: 52%;
    margin: 0 auto;
}

.upload-info {
    text-align: center;
    padding: 8% 20px;
    background-image: url(../images/upload_success_bg.png);
    -webkit-background-size: cover;
    background-size: cover;
    p {
        font-size: 20px;
        font-weight: bold;
        line-height: 1.5;
    }
}

.button-list {
    width: 52%;
    margin: 0 auto;
    margin-top: 40px;
    a {
        padding: 8px 22px;
        color: #fff;
        background-color: #ee344a;
        font-size: 16px;
        &:nth-child(1) {
            float: left;
        }
        &:nth-child(2) {
            float: right;
        }
        &:hover {
            opacity: 0.8;
        }
    }
}