$commonFont: STHeiTi, "Microsoft Yahei", "Helvetica Neue", Helvetica, sans-serif;

@font-face {
    font-family: 'icomoon';
    src: url('../fonts/icomoon.eot?mlbz7y');
    src: url('../fonts/icomoon.eot?mlbz7y#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?mlbz7y') format('truetype'),
    url('../fonts/icomoon.woff?mlbz7y') format('woff'),
    url('../fonts/icomoon.svg?mlbz7y#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

body {
    background-color: #f2f2f2;
    font-family: $commonFont;
    font-size: 15px;
    color: #282828;
}

.container {
    width: 80%;
    max-width: 1080px;
    min-width: 960px;
    margin: 0 auto;
    padding: 20px;
    padding-top: 0;

    & > section {
        margin-top: 20px;
    }
}

.clearfix {
    &::after {
        content: '';
        display: table;
        clear: both;
    }
}

.hide {
    display: none;
}

@mixin inputPlaceholder($color) {
    ::-webkit-input-placeholder {
        color: $color;
    }
    :-moz-placeholder {
        color: $color;
    }
    ::-moz-placeholder {
        color: $color;
    }
    :-ms-input-placeholder {
        color: $color;
    }
}